import http from '@/api/http.js'
const app = getApp();
export default{
	data(){
		return{
			imag: app.globalData.imag,
			list:[],
			page:1
		}
	},
	onLoad() {
		this.getList()
	},
	onShow() {
		this.getList()
	},
	onReachBottom() {
		this.getNewList()
	},
	methods:{
		toMsgDetail(id){
			uni.navigateTo({
				url:'/pages/index/msg/msgDetail?id='+id
			})
		},
		getList(){
			let that = this
			
			let data = {
				token:uni.getStorageSync('token'),
				page:1
			}
			http('/api/Message/getMessageList','post',data).then(res=>{
				if(res.data.status == '200'){
					that.list = res.data.data.data
				}
				console.log(res)
			})
		},
		getNewList(){
			
			let data = {
				token:uni.getStorageSync('token'),
				page:this.page+1
			}
			http('/api/Message/getMessageList','post',data).then(res=>{
				if(res.data.status=='400'){
					this.$util.showToast(res.data.msg)
					return
				}
				
				if(this.page> res.data.data.data.length){
					// this.$util.showToast('没有更多了')	
					return false
				}
				this.page++
				this.list = this.list.concat(res.data.data.data.data)
			})
		}
	}
}
